import React from 'react';
import SectionHeader from '../section-header';
import IconButtonBar from '../icon-button-bar';
import Image from '../image';
import './style.scss';

const PapersSection = ({ papers }) => {
  if (!papers || papers.length < 2) return null;
  return (
    <div className="papers-section-wrapper">
      <div className="papers-section">
        <SectionHeader title="Research" />
        {papers.map((paper, index) =>
          index === 0 ? null : (
            <div className="paper" key={index}>
              <div className="head">
                {paper.title}&nbsp;&nbsp;
                {paper.links && (
                  <IconButtonBar links={paper.links} style={{ color: '#a8a8a8', fontSize: 24 }} />
                )}
              </div>
              <div className="body">
                <Image
                  className="thumbnail"
                  src={paper.thumbnailUrl}
                  alt={paper.thumbnailUrl}
                />
                {paper.techStack && (
                  <div className="tech-stack">
                    {paper.techStack.map((tech, index) => (
                      <div key={index} className="tech">
                        {tech}
                      </div>
                    ))}
                  </div>
                )}
                <div className="description">{paper.description}</div>
              </div>
            </div>
          ),
        )}
      </div>
    </div>
  );
};

export default PapersSection;
